import React, { useEffect, useRef, useState } from "react";
import { Stage, Layer, Image as KonvaImage } from "react-konva";
import useImage from "use-image";
import { EditNumbers } from "..";
import BackspaceOutlinedIcon from '@mui/icons-material/BackspaceOutlined';

const EditField = (props) => {
  const [baseImg] = useImage(props.imageUrl, "anonymous");
  const dragUrl = useRef();
  const [numbers, setNumbers] = useState([]);
  const [baseImg_h, setBaseImg_h] = useState(0);
  const [baseImg_w, setBaseImg_w] = useState(0);
  const [baseImgRatio, setBaseImgRatio] = useState(0);
  const [dragJudge, setDragJudge] = useState(0);

  const returnBase = () => {
    setNumbers(numbers.filter((image, index) => index !== numbers.length - 1));
  };

  useEffect(() => {
    // 画像のレスポンシブ表示に関しての処理
    if (baseImg) {
      const magnification = window.innerWidth  / baseImg.width;
      const imgRatio = baseImg.height / baseImg.width;
      setBaseImgRatio(imgRatio);
      setBaseImg_w(window.innerWidth * 0.95);
      setBaseImg_h((baseImg.height * magnification) * 0.95);
    }
  }, [baseImg]);

  if(baseImgRatio < 1){
    // 正方形より横長な画像用
    return (
      <div className="editFieldContainer">
        <div className="numbersSlider">
          <button className="returnButton" onClick={returnBase}>
            <BackspaceOutlinedIcon
              className="returnInnerButton"
              sx={{
                color: "#333333",
                fontSize: 32,
              }}
            />
          </button>
          <div className="numbersCover">
            {(() => {
              const items = [];
              for(let i = 1;i <= 9;i++){
                items.push(<div className="circleNumber" key={i}><p  draggable="true" onDragStart={(e) => {setDragJudge(i)}}>
                  {i}
                  </p></div>)
              }
              return(items);
            })()}
          </div>

        </div>
        <div
          onDrop={(e) => {
            e.preventDefault();
            // register event position
            props.correctImgRef.current.setPointersPositions(e);
            // add image
            setNumbers(
              numbers.concat([
                {
                  ...props.correctImgRef.current.getPointerPosition(),
                  src: dragUrl.current,
                  dragJudge: dragJudge,
                },
              ])
            );
          }}
          onDragOver={(e) => e.preventDefault()}
        >
          <div className="konva-cover-pc" id="canvasArea">
            <Stage
              width={baseImg_w}
              height={baseImg_h}
              ref={props.correctImgRef}
            >
              <Layer>
                <KonvaImage
                  className="image-konva"
                  image={baseImg}
                  width={baseImg_w}
                  height={baseImg_h}
                />
                {numbers.map((image, index) => {
                  return <EditNumbers image={image} dragJudge={dragJudge} key={index} />;
                })}
              </Layer>
            </Stage>
          </div>
        </div>
      </div>
    );
  }else {
    // 正方形より縦長な画像用
    return (
      <div className="editFieldContainer">
        <div className="numbersSlider">
          <button className="returnButton" onClick={returnBase}>
            <BackspaceOutlinedIcon
              className="returnInnerButton"
              sx={{
                color: "#333333",
                fontSize: 32,
              }}
            />
          </button>
          <div className="numbersCover">
            {(() => {
              const items = [];
              for(let i = 1;i <= 9;i++){
                items.push(<div className="circleNumber" key={i}><p  draggable="true" onDragStart={(e) => {setDragJudge(i)}}>
                  {i}
                  </p></div>)
              }
              return(items);
            })()}
          </div>

        </div>
        <div
          onDrop={(e) => {
            e.preventDefault();
            // register event position
            props.correctImgRef.current.setPointersPositions(e);
            // add image
            setNumbers(
              numbers.concat([
                {
                  ...props.correctImgRef.current.getPointerPosition(),
                  src: dragUrl.current,
                  dragJudge: dragJudge,
                },
              ])
            );
          }}
          onDragOver={(e) => e.preventDefault()}
        >
          <div className="konva-cover-pc" id="canvasArea">
            <Stage
              width={baseImg_w}
              height={baseImg_h}
              ref={props.correctImgRef}
            >
              <Layer>
                <KonvaImage
                  className="image-konva"
                  image={baseImg}
                  width={baseImg_w}
                  height={baseImg_h}
                />
                {numbers.map((image, index) => {
                  return <EditNumbers image={image} dragJudge={dragJudge} key={index} />;
                })}
              </Layer>
            </Stage>
          </div>
        </div>
      </div>
    );
  }
};

export default EditField;
