import React, { useCallback, useEffect, useRef, useState } from "react";
import ReactTags from "react-tag-autocomplete";
import "./assets/styles/tagInput.css";
import { useNavigate } from "react-router-dom";
import { collection, addDoc, query, where, getDocs } from "firebase/firestore";
import { EditField } from "..";
import { getAuth } from "firebase/auth";
import { ref, deleteObject, uploadString } from "firebase/storage";
import { storage, db } from "../../firebase";

const SecondArticleDisplay = (props) => {
  const correctImgRef = useRef(null);

  const onImgChange = async (img, imageNameSnapshot) => {
    if(img) {
      // imgCountを受け取って+1して返す
      const storageRef = ref(storage, "images/" + imageNameSnapshot);
      uploadString(storageRef, img, 'data_url');

      // 元の画像を削除
      const articleImgRef = ref(storage, 'images/' + props.imageName);
      deleteObject(articleImgRef).then(() => {
        // File deleted successfully
        navigate('/hApp-test/userLibrary');
      }).catch((error) => {
        // Uh-oh, an error occurred!
        console.log("元画像の削除に失敗しました" + error);
      });
    } else {
      alert("編集した画像がありません！！");
      navigate("/hApp-test");
    }
  }

  const handleSubmit = async (event) => {
    event.preventDefault();
    const authObject = getAuth();
    const user = authObject.currentUser;
    const { articleBody } = event.target.elements;
    if (user !== null) {
      // 画像の数を数えて、それを画像の名前の中央へ
      const imgsCollectionRef = collection(db, "imgs");
      const imgsSnapshot = await getDocs(imgsCollectionRef);
      const imgsSum = imgsSnapshot.docs.length;
      const num = () => Math.floor(Math.random() * 10000000000);
      const imageNameSnapshot = num() + "_" + imgsSum + "_" + num();
      // 編集した画像をアップロード
      const uri = correctImgRef.current;
      const canvasImg = uri.toDataURL();
      await onImgChange(canvasImg, imageNameSnapshot);
      // 投稿をデータベースに保存
      const usersCollectionRef = collection(db, "corrections");
      const documentRef = await addDoc(usersCollectionRef, {
        user_id: user.uid,
        body: articleBody.value,
        image_name: imageNameSnapshot,
      });

      // タグと記事を結びつける
      tagsId.forEach(async (tag) => {
        const tagsCollectionRef = collection(db, "attachedTag");
        // eslint-disable-next-line no-unused-vars
        const tagsDocumentRef = await addDoc(tagsCollectionRef, {
          article_id: documentRef.id,
          tag_id: tag,
        });
      });
      // eslint-disable-next-line no-unused-vars
      const imgsDocumentRef = await addDoc(imgsCollectionRef, {
        image_name: imageNameSnapshot,
        method_id: documentRef.id,
        method: "article",
      });
    }
  };

  // 添削画像を更新すると更新される
  useEffect(() => {
  },[correctImgRef])

  const [tagsId, setTagsId] = useState([]);
  const [tags, setTags] = useState([]);
  const navigate = useNavigate();
  const reactTags = useRef();

  const onDelete = useCallback(
    async (tagIndex) => {
      console.log(tagIndex);
      const newTags = tags.filter((tag, index) => index !== tagIndex);
      setTags(newTags);

      const tagsCorrectionRef = collection(db, "tags");
      const q = query(
        tagsCorrectionRef,
        where("tag_name", "==", tags[tagIndex].name)
      );
      const querySnapshot = await getDocs(q);
      console.log(querySnapshot);
      querySnapshot.forEach(async (document) => {
        console.log(document.id);
        setTagsId(tagsId.filter((n) => n !== document.id));
      });
    },
    [tags, tagsId]
  );

  const onAddition = useCallback(
    async (newTag) => {
      const newTags = [].concat(tags, newTag);
      setTags(newTags);

      const tagsCorrectionRef = collection(db, "tags");
      const q = query(tagsCorrectionRef, where("tag_name", "==", newTag.name));
      const querySnapshot = await getDocs(q);
      // データベースのtagを確認、なかったら追加。
      if (!querySnapshot.docs.length) {
        // eslint-disable-next-line no-unused-vars
        const documentRef = await addDoc(tagsCorrectionRef, {
          tag_name: newTag.name,
        });
        setTagsId([...tagsId, documentRef.id]);
      } else {
        console.log("そのtagはもうあるよ");
        setTagsId([...tagsId, querySnapshot.docs[0].id]);
      }
    },
    [tags, tagsId]
  );

  if (props.imageUrl) {
    return (
      <div className="articleField">
        <div>
          <EditField imageUrl={props.imageUrl} correctImgRef={correctImgRef} />
        </div>
        <form onSubmit={handleSubmit}>
          <div className="articleFormContainer">
          <div className="articleBodyContainer">
              <textarea
                name="articleBody"
                type="text"
                placeholder="テキストを入力してください"
              ></textarea>
            </div>
            <div className="tagContainer">
              <ReactTags
                ref={reactTags}
                tags={tags}
                onAddition={onAddition}
                onDelete={onDelete}
                allowNew
              />
            </div>
            <input type="submit" value="新規投稿"></input>
          </div>
        </form>
      </div>
    );
  }else {
    navigate("/hApp-test");
  }
};

export default SecondArticleDisplay;
