import React, { useState } from "react";
import { Text, Circle } from "react-konva";

const EditNumbers = (props) => {
  const [dragImg, setDragImg] = useState({
    isDragging: false,
    text: props.image.dragJudge,
    x: props.image.x,
    y: props.image.y,
  });

  return (
    <>
    <Circle stroke='black' strokeWidth={2} x={dragImg.x + 4} y={dragImg.y + 6} width={25} height={25} />
      <Text
        text={dragImg.text}
        fontStyle="bold"
        fontSize="14"
        x={dragImg.x}
        y={dragImg.y}
      />
    </>
  );
};

export default EditNumbers;