import React, { useState } from "react";
import { storage, db } from "../firebase";
import { ref, uploadBytesResumable, getDownloadURL } from "firebase/storage";
import { collection, getDocs } from "firebase/firestore";
import "./assets/styles/base.css";
import { useEffect, useRef } from "react";
import ArrowBackIosNewIcon from "@mui/icons-material/ArrowBackIosNew";
import { useNavigate } from "react-router-dom";

const NewPost = (props) => {
  // ユーザーからの画像を取得
  const [imageUrl, setImageUrl] = useState("");
  const [imageName, setImageName] = useState("");
  // const [open, setOpen] = useState("");
  const navigate = useNavigate();

  const onFileChange = async (img) => {
    const image = img.target.files[0];

    console.log("アップロード処理");
    const metadata = {
      contentType: "image/jpeg",
    };

    // 画像の数を数えて、それを画像の名前の中央へ
    const imgsCollectionRef = collection(db, "imgs");
    const imgsSnapshot = await getDocs(imgsCollectionRef);
    const imgsSum = imgsSnapshot.docs.length;
    const num = () => Math.floor(Math.random() * 10000000000);
    const imageNameSnapshot = num() + "_" + imgsSum + "_" + num();
    setImageName(imageNameSnapshot);

    // imgCountを受け取って+1して返す
    const storageRef = ref(storage, "images/" + imageNameSnapshot);
    const uploadTask = uploadBytesResumable(storageRef, image, metadata);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
      },
      (error) => {
        console.log("err", error);
      },
      () => {
        getDownloadURL(uploadTask.snapshot.ref).then((downloadURL) => {
          console.log("File available at", downloadURL);
          setImageUrl(downloadURL);
        });
      }
    );
  };

  useEffect(() => {
    if (imageUrl) {
      navigate("/hApp-test/createArticle", {
        state: { imageUrl: imageUrl, imageName: imageName },
      });
    }
  }, [imageUrl, props.newPostOpen]);

  const insideRef = useRef(null);

  useEffect(() => {
    //対象の要素を取得
    const el = insideRef.current;

    //対象の要素がなければ何もしない
    if (!el) return;

    //クリックした時に実行する関数
    const hundleClickOutside = (e) => {
      if(props.newPostClick) {
        props.setNewPostOpen(true);
        // 新規投稿ボタンを押したかどうか判断
        props.setNewPostClick(false);
      } else {
        if (!el?.contains(e.target)) {
          //ここに外側をクリックしたときの処理
          if(props.newPostOpen) {
            props.setNewPostOpen(false);
          }
        } else {
          //ここに内側をクリックしたときの処理
        }
      }
    };

    //クリックイベントを設定
    document.addEventListener("click", hundleClickOutside);

    //クリーンアップ関数
    return () => {
      //コンポーネントがアンマウント、再レンダリングされたときにクリックイベントを削除
      document.removeEventListener("click", hundleClickOutside);
    };
  }, [insideRef, props.newPostClick]);

  const moveNewPost = () => {
    props.setNewPostOpen(false);
  };

  return (
    <>
      {/* <Transition
      in={props.newPostOpen}
      timeout={550}> */}
      <div ref={insideRef} className={props.newPostOpen ? "newPostContainer " : "close"}>
        <ul>
          <li>
            <label>
              画像アップロード
              <input
                type="file"
                name="selectImg"
                accept="image/*"
                className="newInputFile"
                onChange={(img) => {
                  onFileChange(img);
                }}
              />
            </label>
          </li>
          <li>フォルダ新規作成</li>
          <li>
            <button className="newPostButton" onClick={moveNewPost}>
              <ArrowBackIosNewIcon
                className="backButton"
                sx={{
                  color: "#333333",
                  fontSize: 20,
                  transform: "rotate(90deg)",
                }}
              />
            </button>
          </li>
        </ul>
      </div>

      {/* </Transition> */}
    </>
  );
};

export default NewPost;
